import * as THREE from 'three';
import { RendererThree } from '../../../../../lib';

export class Solution extends RendererThree {

    pointLight: THREE.PointLight

    constructor(id: string = 'main') {
        super(id)


        this.start();
    }

    public update(scene: THREE.Scene) {


    }

}

export const renderPoints = (scene: THREE.Scene, pos: number[], colors: number[]) => {
    const geometry = new THREE.BufferGeometry();
    const material = new THREE.PointsMaterial({ vertexColors: true, size: 0.5 });
    geometry.setAttribute('position', new THREE.Float32BufferAttribute(pos, 3));
    geometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
    geometry.computeBoundingSphere();
    const mesh = new THREE.Points(geometry, material);
    scene.add(mesh);
};
